@import "scaffolding.scss";

p {
    margin-top: 0;
    padding-left: 30px;

    font-size: 20px;
}

a {
    text-decoration: none;

    &:hover {
        color: blue;
        opacity: 0.4;
    }
}

span {
    color: rgb(86, 129, 146);
}

.container {
    width: 100%;
    height: auto;
    margin-left: 2%;
    margin-bottom: 10%;

    position: relative;

    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}

.wrapper {
    width: 100vw;
    max-width: 800px;
    margin: 0 auto;
}

.page-main {
    width: 100vw;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.main-wrapper {
    width: 80%;
    max-width: 800px;
    min-height: 400px;

    display: flex;
    flex-direction: row;
}

.menu-list {
    background-color: green;
}

.slide-wrapper {
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    position: relative;
    overflow: hidden;
}

.slide {
    width: 100%;
    height: 100%;

    position: relative;
    top: 0;

    flex-shrink: 0;

    transition: 600ms all ease-in-out;

    &--1 {
        left: -100%;
    }
    
    &--2 {
        left: -100%;
    }

    &--3 {
        left: -100%;
    }
}

input {
    display: none;
}

.button {
    &:checked {
        display: none;
    }
}

.button-0 {
    &:checked {
        + input + input + div .button-label:nth-child(1) {
            display: none;
        }

         + input + input + div + div div {
            left: -100%;
        }

         + input + input + div + div div + div {
            left: -100%;
        }

         + input + input + div + div div + div + div {
            left: -100%;
        }
    }
}

.button-1 {
    &:checked {
        + input + div .button-label:nth-child(2) {
            display: none;
        }

         + input + div + div div + div {
            left: -200%;
        }

         + input + div + div div + div + div {
            left: -200%;
        }
    }
}

.button-2 {
    &:checked {
        + div .button-label:nth-child(3) {
            display: none;
        }

        + div + div div {
            left: 0;
        }

        + div + div div + div{
            left: 0;
        }
    }
}

.button-label {
    width: 100%;
    min-width: 150px;
    height: 50px;

    display: block;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 28px;
    color: white;
    text-transform: uppercase;
    background-color: rgba(172, 172, 172, 1);
    cursor: pointer;

    border: solid 2px white;
    transition: 100ms all ease-in-out;

    box-sizing: border-box;

    &:hover {
        opacity: 0.7;
    }
}

.nav-container {
    max-width: 150px;

    display: flex;
    flex-direction: column;
}

.photo-wrapper {
    margin: 0 auto;
    margin-bottom: 13%;
    width: 50vw;
    height: 50vw;
    max-width: 300px;
    max-height: 300px;

    background-image: url("../img/ava.jpg");
    background-size: cover;
    border-radius: 50%;
    background-color: grey;

    opacity: 0;
    transition: 600ms opacity ease-in-out;
}

.animated-black {
    width: 80%;

    position: relative;
    opacity: 0.8;
    z-index: 10;
}

.animated-blue {
    width: 80%;
    margin-left: -1%;

    position: absolute;
    top: 0;
    z-index: 1;
}

@media (max-width: 500px) {
    p {
        padding-left: 0;
    }

    .button-label {
        width: 100%;
    }

    .nav-container {
        max-width: 100%;
        padding-bottom: 15%;
    }

    .photo-wrapper {
        margin-bottom: 7%;
    }

    .main-wrapper {
        flex-direction: column;
    }
}

